





















import { Component, Vue } from "vue-property-decorator";
import { routes } from "../router/index";
@Component({
  components: {}
})
export default class Home extends Vue {
  private data: any = [];
  defaultProps: any = {
    children: "children",
    label: "path"
  };
  created() {
    console.log(routes);
    this.data = routes;
  }
  handleNodeClick(data: any) {
    console.log(data);
    if (!data.children) {
      this.$router.push({
        name: data.name
      });
    }
  }
}
